/*@import url('https://fonts.googleapis.com/css2?family=Finger+Paint&display=swap');*/
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    /*font-family: 'Finger Paint', cursive;*/
    font-family: 'Lato', sans-serif;
}

.main {
    background: url("../images/subBg.jpeg") no-repeat center;
    background-size: cover;
    position: relative;
}

.bg-blur {
    display: none;
}

.main .container {
    width: 100vw;
    min-height: calc(100vh - 240px);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
}

.header-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.header-container .logo-img {
    width: 100%;
    padding: 5px 5px 5px 36px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.header-container .logo-img img {
    z-index: 1;
    width: 70px;
    height: 70px;
    border-radius: 100%;
    object-fit: cover;
}

.header-container .header-btn {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-container .header-btn .btn {
    background: linear-gradient(to right, #240b36, #c31432);
    padding: 15px;
    border-radius: 10px;
    font-size: 15px;
    color: #ffffff;
    font-weight: bold;
    text-decoration: none;
    z-index: 1;
}

.header-container .header-btn .btn:hover {
    background: #FF0099;
    background: -webkit-linear-gradient(to right, #493240, #FF0099);
    background: linear-gradient(to right, #493240, #FF0099);
}

.main .left-side, .main .right-side {
    height: 100%;
}

.main .left-side {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main .left-side img {
    width: 250px;
    object-fit: cover;
}

.right-side {
    display: flex;
    justify-content: center;
    align-items: center;
}

.right-side .mint-box__container {
    color: #ffffff;
    text-align: center;
    border: 1.5px solid #FDA7DF;
    padding: 24px 12px;
    border-radius: 12px;
    width: 90%;
    background-color: rgba(0, 0, 0, .4);
}

.right__col .title, .right__col .subtitle, .right__col .countdown-time {
    text-transform: uppercase;
    font-weight: bold;
}

.right__col .title {
    font-size: clamp(25px, 3vw, 35px);
}

.right__col .subtitle, .right__col .countdown-time {
    font-size: clamp(15px, 3vw, 25px);
    margin-top: 12px;
}

.right__col .count {
    margin-top: 12px;
}

.right__col .btn-mint__btn {
    margin-top: 12px;
    border: none;
    outline: none;
    color: #ffffff;
    width: 150px;
    background: #c31432;
    background: -webkit-linear-gradient(to right, #240b36, #c31432);
    background: linear-gradient(to right, #240b36, #c31432);
    padding: 12px;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color .2s linear;
    font-size: 14px;
    font-weight: bold;
}

.right__col .btn-mint__btn:hover {
    background: #FF0099;
    background: -webkit-linear-gradient(to right, #493240, #FF0099);
    background: linear-gradient(to right, #493240, #FF0099);
}

.right__col .err__msg {
    margin-top: 12px;
}

.right__col .buy__wrapper {
    margin-top: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.right__col .buy__wrapper .btn-wrapper {
    width: 50px;
    margin-top: unset;
    height: 30px;
    padding: unset;
}

.right__col .buy__wrapper .count-number {
    height: 30px;
    padding: 12px;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #c31432;
    background: -webkit-linear-gradient(to right, #240b36, #c31432);
    background: linear-gradient(to right, #240b36, #c31432);
}

.footer-container {
    min-height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-container .txt {
    font-size: clamp(14px, 5vw, 16px);
    text-align: center;
    margin: 12px 0;
    color: #ffffff;
    z-index: 1;
    padding: 0 12px;
}

.footer-container .cprt {
    color: #cc71d8;
    text-align: center;
    font-size: clamp(10px, 5vw, 12px);
    margin-bottom: 12px;
    z-index: 1;
}

@media (max-width: 767px) {
    .main {
        background: url("../images/bgNew.png") no-repeat center;
        background-size: cover;
    }

    .main .container {
        grid-template-columns: 1fr;
        min-height: calc(100vh - 340px);
    }

    .header-container {
        grid-template-columns: 80px 1fr;
    }

    .bg-blur {
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .4);
        left: 0;
        top: 0;
        position: absolute;
        z-index: 0;
    }

    .mint-box__container {
        width: 100%;
        padding: 24px 0;
        z-index: 1;
    }

    .left-side {
        display: none !important;
    }

    .logo-img {
        display: none;
    }

    .right__col .title span, .right__col .countdown-time span, .right__col .subtitle span {
        color: #FDA7DF !important;
    }

    .footer-container {
        min-height: 260px;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .main .container {
        min-height: calc(100vh - 260px);
    }

    .footer-container {
        min-height: 180px;
    }
}

